ext-decoration: none;\n  outline: none;\n}\n\n/* //Reset Code */\n\n/* colors code */\n\n.text-bl {\n  color: #343a40;\n}\n\n.text-wh {\n  color: #fff;\n}\n\n.text-li {\n  color: #353e47;\n}\n\n.bg-li {\n  background: #f8f9fa;\n}\n\n.bg-wh {\n  background: #fff;\n}\n\n.let {\n  letter-spacing: 1px;\n}\n\n/* //colors code */\n\n/* move top icon */\n\na.move-top {\n  width: 34px;\n  height: 34px;\n  /*background: url(../images/move-top.png) no-repeat;*/\n  background: #0b2e13;\n  display: inline-block;\n  position: fixed;\n  bottom: 4%;\n  right: 2%;\n  z-index: 0;\n}\n\n/* //move top icon */\n\n/* header */\n\n/* top header */\n\n.header-top_w3layouts {\n  padding: 0 0.5em;\n}\n\n/* social icons */\n\n.top-right-info li {\n  display: inline-block;\n}\n\n.top-right-info li span {\n  color: #343a40;\n  font-size: 14px;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n  opacity: 0.7;\n}\n\n.top-right-info li span:hover,\na.login-button-2:hover {\n  color: #343a40;\n  opacity: 1;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n}\n\n/* //social icons */\n\n.header-top_w3layouts p,\n.header-w3layouts p,\n.top-w3layouts p {\n  font-size: 14px;\n}\n\n.top-w3layouts p a:hover,\n.w3l-footer ul li a:hover {\n  opacity: 0.7;\n}\n\na.login-button-2 {\n  letter-spacing: 1px;\n  font-size: 14px;\n  font-weight: 500;\n  opacity: 0.7;\n}\n\n/* //top header */\n\n/* second header */\n\n#home {\n  position: relative;\n}\n\n.main-top {\n  position: absolute;\n  z-index: 999;\n  width: 100%;\n  background: rgba(0, 0, 0, 0.4);\n}\n\n.main-top-home {\n  position: absolute;\n  z-index: 999;\n  width: 100%;\n  height: 10%;\n  background: rgba(0, 0, 0, 0.4);\n}\n\n/* navigation */\n\n/* CSS Document */\n\n.toggle-2,\n.toggle,\n[id^=\"drop\"] {\n  display: none;\n}\n\n/* Giving a background-color to the nav container. */\n\nnav {\n  margin: 0;\n  padding: 0;\n}\n\n/* logo */\n\n#logo a {\n  float: left;\n  display: initial;\n  font-weight: 700;\n  font-size: 32px;\n  color: #fff;\n  text-shadow: 2px 5px 3px rgba(0, 0, 0, 0.06);\n  padding: 0;\n}\n\nspan.logo-sp {\n  font-weight: 100;\n  color: white;\n}\n\nspan.logo-sp2 {\n  font-weight: 100;\n  margin-left: 20px;\n  background: #1e7e34;\n}\n\n/* //logo */\n\n/* Since we'll have the \"ul li\" \"float:left\"\n * we need to add a clear after the container. */\n\nnav:after {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n\n/* Removing padding, margin and \"list-style\" from the \"ul\",\n * and adding \"position:reltive\" */\n\nnav ul {\n  float: right;\n  padding: 0;\n  margin: 0;\n  list-style: none;\n  position: relative;\n}\n\n/* Positioning the navigation items inline */\n\nnav ul li {\n  margin: 0px;\n  display: inline-block;\n  float: left;\n}\n\n/* Styling the links */\n\nnav a {\n  padding: 0;\n  color: #fff;\n  font-size: 13px;\n  letter-spacing: 2px;\n  font-weight: 700;\n  text-transform: uppercase;\n  /* display: inline-block; */\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n}\n\nnav ul li ul li:hover {\n  color: #000;\n  background: #f8f9fa;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n}\n\n/* Background color change on Hover */\n\n.menu li a.active,\n.menu li a:hover {\n  color: #9beafd;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n}\n\n.menu li a.activeBefore {\n  color: #9beafd;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n  padding-right: 3.9em;\n}\n\n.menu li a.contUs {\n  padding-left: 1em;\n  color: white;\n  cursor: pointer;\n}\n\n/* Hide Dropdowns by Default\n * and giving it a position of absolute */\n\nnav ul ul {\n  display: none;\n  position: absolute;\n  top: 25px;\n  background: #000;\n  padding: 10px;\n  border-radius: 4px;\n  z-index: 9;\n}\n\n/* Display Dropdowns on Hover */\n\nnav ul li:hover > ul {\n  display: inherit;\n}\n\n/* Fisrt Tier Dropdown */\n\nnav ul ul li {\n  width: 170px;\n  float: none;\n  display: list-item;\n  position: relative;\n  margin-bottom: 10px;\n  transition: 0.5s all;\n  -webkit-transition: 0.5s all;\n  -moz-transition: 0.5s all;\n  -o-transition: 0.5s all;\n  -ms-transition: 0.5s all;\n}\n\nnav ul ul li:nth-child(4) {\n  margin-bottom: 5px;\n}\n\nnav ul ul li a {\n  color: rgba(255, 255, 255, 0.459);\n  padding: 5px 10px;\n  display: block;\n  font-size: 12px;\n  letter-spacing: 1px;\n}\n\n/* Second, Third and more Tiers\n * We move the 2nd and 3rd etc tier dropdowns to the left\n * by the amount of the width of the first tier.\n*/\n\nnav ul ul ul li {\n  position: relative;\n  top: -60px;\n  /* has to be the same number as the \"width\" of \"nav ul ul li\" */\n  left: 170px;\n}\n\n/* Change ' +' in order to change the Dropdown symbol */\n\nli > a:only-child:after {\n  content: \"\";\n}\n\na.reqe-button {\n  border: 2px solid rgba(255, 255, 255, 0.35);\n  border-radius: 4px;\n}\n\n/* Media Queries\n--------------------------------------------- */\n\n@media (max-width: 800px) {\n  nav a {\n    font-size: 12px;\n  }\n}\n\n@media all and (max-width: 767px) {\n  #logo {\n    display: block;\n    padding: 0;\n    width: 100%;\n    text-align: center;\n    float: none;\n  }\n\n  nav {\n    margin: 0;\n  }\n\n  /* Hide the navigation menu by default */\n  /* Also hide the  */\n  .toggle + a,\n  .menu {\n    display: none;\n  }\n\n  /* Stylinf the toggle lable */\n  .toggle {\n    display: block;\n    padding: 8px 14px;\n    font-size: 15px;\n    text-decoration: none;\n    border: none;\n   